:root {
  --bg: #0b1020;
  --card: #111833;
  --muted: #9aa4bf;
  --text: #e8ecf7;
  --accent: #4f8cff;
  --ok: #2ecc71;
  --warn: #f39c12;
  --danger: #e74c3c;
  --border: #1b2347;
}

* {
  box-sizing: border-box;
}

html, body {
  background: var(--bg);
  color: var(--text);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Helvetica Neue, Noto Sans SC, Arial, PingFang SC, Hiragino Sans GB, Microsoft YaHei, sans-serif;
  line-height: 1.5;
}

.container {
  max-width: 980px;
  margin: 32px auto;
  padding: 0 16px;
}

h1 {
  margin: 0 0 16px;
  font-size: 28px;
}

.header-row {
  flex-direction: column;
  align-items: stretch;
  gap: 10px;
  margin-bottom: 12px;
  display: flex;
}

.title-wrap {
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  display: flex;
}

.site-logo {
  border-radius: 6px;
  width: 30%;
  height: 30%;
  display: block;
}

@media screen and (width <= 640px) {
  .site-logo {
    width: 30vw;
  }
}

.logo-row {
  align-items: center;
  display: flex;
}

.title-row {
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  display: flex;
}

.lang-control select {
  color: var(--text);
  border: 1px solid var(--border);
  background: #0e1530;
  border-radius: 10px;
  padding: 8px 12px;
}

.visually-hidden {
  clip: rect(0, 0, 1px, 1px);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h2 {
  color: var(--muted);
  margin: 0 0 12px;
  font-size: 18px;
  font-weight: 600;
}

section {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 12px;
  margin-bottom: 16px;
  padding: 16px;
}

.field-group {
  margin-bottom: 12px;
}

.field-group label {
  color: var(--text);
  margin-bottom: 6px;
  display: block;
}

.inline-fields {
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  display: grid;
}

.field {
  position: relative;
}

.field input {
  color: var(--text);
  border: 1px solid var(--border);
  background: #0e1530;
  border-radius: 10px;
  outline: none;
  width: 100%;
  padding: 10px 12px;
  transition: border-color .2s cubic-bezier(.22, .61, .36, 1), box-shadow .2s cubic-bezier(.22, .61, .36, 1), background-color .2s cubic-bezier(.22, .61, .36, 1);
}

.field input:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px #4f8cff33;
}

.field.with-unit input {
  padding-right: 54px;
}

.suffix {
  color: var(--muted);
  pointer-events: none;
  font-size: 12px;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
}

.actions {
  margin-top: 8px;
}

.actions button {
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: 10px 14px;
  transition: transform .16s cubic-bezier(.22, .61, .36, 1), filter .16s cubic-bezier(.22, .61, .36, 1), box-shadow .16s cubic-bezier(.22, .61, .36, 1);
}

.actions #calculateBtn {
  background: var(--ok);
}

.actions #resetBtn {
  background: var(--danger);
}

.actions button:hover {
  filter: brightness(1.05);
  transform: translateY(-1px);
  box-shadow: 0 6px 16px #00000040;
}

.actions button:active {
  filter: brightness(.98);
  transform: translateY(0);
  box-shadow: 0 3px 10px #0003;
}

.results-grid {
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
  display: grid;
}

.result-item {
  border: 1px solid var(--border);
  background: #0e1530;
  border-radius: 10px;
  padding: 12px;
  transition: transform .2s cubic-bezier(.22, .61, .36, 1), box-shadow .2s cubic-bezier(.22, .61, .36, 1), background-color .2s cubic-bezier(.22, .61, .36, 1), border-color .2s cubic-bezier(.22, .61, .36, 1);
}

.result-item .label {
  color: var(--muted);
  margin-bottom: 6px;
  font-size: 12px;
}

.result-item .value {
  font-size: 18px;
  font-weight: 700;
}

.result-item .list-value {
  white-space: pre-wrap;
  font-size: 14px;
}

.result-item.span-2 {
  grid-column: span 2;
}

@keyframes bump {
  0% {
    border-color: var(--border);
    transform: scale(1);
    box-shadow: 0 0 #4f8cff00;
  }

  40% {
    border-color: #4f8cff99;
    transform: scale(1.015);
    box-shadow: 0 8px 24px #4f8cff26;
  }

  100% {
    border-color: var(--border);
    transform: scale(1);
    box-shadow: 0 0 #4f8cff00;
  }
}

.result-item.bump {
  animation: .36s cubic-bezier(.22, .61, .36, 1) bump;
}

.note {
  color: var(--muted);
  margin-top: 12px;
  font-size: 12px;
}

@media (width <= 640px) {
  .results-grid, .inline-fields {
    grid-template-columns: 1fr;
  }
}

.footer {
  text-align: center;
  color: var(--muted);
  margin: 12px 0 24px;
  font-size: 12px;
}
